/*---------------Burger menu (Navbar)------------------*/
.burgerMenu {
	margin: 2.8rem 0rem 0rem 0rem;
	display: none;
	@include respond(tablet) {
		display: none;
	}
	ul {
		top: 0;
		margin-top: 3.4rem;
		width: 18.9rem;
		position: absolute;
		right: 0;
		@include respond(tablet) {
			display: flex;
			justify-content: flex-end;
			width: 84rem;
			top: 0;
			right: 0;
			position: fixed;
			margin: 3rem 0;
		}
	}
	li {
		list-style-type: none;
	}
	a {
		display: flex;
		padding: 0.8rem;
		border: 1px solid white;
		width: 17rem;
		height: 1.5rem;
		justify-content: flex-end;
		font-size: 1.6rem;
		line-height: 1rem;
		background: #110a6b;
		text-decoration: none;
		color: white;
		@include respond(tablet) {
			font-family: Arial, Helvetica, sans-serif;
			width: 6rem;
			border: none;
			font-weight: 600;
			padding-top: 1.2rem;
			display: flex;
			background: none;
			justify-content: center;
			font-size: 1.2rem;
		}
	}
}
a:hover {
	font-style: italic;
	color: lightcyan;
}
.burgerMenu--display {
	display: block;
}
